import React, { useRef, useEffect } from 'react'


import useWindowDimensions from '../../hooks/useWindowDimensions'
import { Canvas } from './BackCanvas.style'
import { noise } from './perlin'

const BackCanvas: React.FC<{}> = () => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null)
  const canvasCtxRef = React.useRef<CanvasRenderingContext2D | null>(null)
  const { height, width } = useWindowDimensions()
  const points = 150
  const lines = 18
  const smoothness = 10
  const lineDistance = 40
  useEffect(() => {
    // Initialize
    if (canvasRef.current) {
      canvasCtxRef.current = canvasRef.current.getContext('2d')
      const ctx = canvasCtxRef.current
      for (let j = 1; j < lines; j++) {
        ctx!.beginPath()
        for (let i = 0; i < points; i++) {
          ctx!.strokeStyle = '#8360eb63'
          const num = noise(i / smoothness) * j * lineDistance
          if (i === 0) 
            ctx!.moveTo(0, num)
          else
            ctx!.lineTo(i * document.body.clientWidth / points, num)
        }
        ctx!.stroke()
      }
    }
  }, [width, height])

  return <Canvas ref={canvasRef} width={width} height={height} />
}



export default BackCanvas