import React, { useEffect, useState } from 'react'

// import { TransitionGroup } from 'react-transition-group'

import '@animxyz/core'
import { XyzTransitionGroup } from '@animxyz/react'
import { Protocol } from 'multichain-sdk'

import { Tooltip } from '../UIElements/Tooltip'
import * as Styled from './ProtocolSelect.style'

import './ProtocolSelect.css'

interface ProtocolState{
    protocol:Protocol;
    enabled:boolean;
    color: string;
    
}

const ProtocolDetails: { [key: string]: ProtocolState } = {
    thorchain:  { protocol:'thorchain', enabled: true, color: '23DCC888' },
    anyswap: { protocol:'anyswap', enabled: true, color: '685ef288' },
    chainflip: { protocol:'chainflip', enabled: true, color: 'ff3cab88' },
    sifchain: { protocol:'sifchain', enabled: true, color: 'caa93a88' }, 
}

export const ProtocolSelect = () => {
    const [selectedItems] = useState<{ [key: string]: ProtocolState }>(ProtocolDetails)
 
    const [show, setShow] = useState(false)
    useEffect(() => {
      setTimeout(()=>setShow(true), 500)
    }, [selectedItems])
    return (
        <div>
            <div>
            <XyzTransitionGroup
              duration={2000}
              appear 
              xyz='stagger-2'
              className='item-group'
            >
                    {Object.entries(selectedItems).map(([key, item]) => ( 
                            <div className='invis' key={item.protocol}>
                                <Tooltip key={item.protocol} tooltip="Coming Soon" placement="topLeft" title="Coming Soon">
                                    <Styled.ProtocolTag key={item.protocol} onClick={()=>{item.enabled = false}} style={{ display: !show ? 'hidden' : 'block', borderColor: !item.enabled ? 'lightgray' : Styled.colors[item.protocol] }}>
                                        {key}
                                    </Styled.ProtocolTag>
                                </Tooltip>
                            </div>
                    ))}
                    
            </XyzTransitionGroup>
            </div>
        </div>
    )
}